import { LitElement, html, css } from 'lit';
import { customElement, property, } from 'lit/decorators.js'
import './accounts/add';
import './accounts/view';


@customElement("pdb-connections-accounts")
export class PdbConnectionsAccounts extends LitElement {
  constructor() { super(); }

  connectedCallback() {
    super.connectedCallback();
  }

  disconnectedCallback() { }
  static styles = [
    css`
   :host { display: block; }
  `
  ];

  render() {

    return html`
      <div class="flex flex-col">
        <pdb-connections-accounts-add class="my-1"></pdb-connections-accounts-add>
        <pdb-connections-accounts-view class="my-1"></pdb-connections-accounts-view>
      </div>
    `;
  }

  firstUpdated() { }

  createRenderRoot() {
    return this;
  }
}
